import { Injectable, Input, ElementRef } from '@angular/core';
import { DataStorageService } from '../data-storage/data-storage.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';
import { fromEvent } from 'rxjs';
import { AnalyticsService } from '../analytics/analytics.service';

@Injectable({
  providedIn: 'root'
})
export class PointerService {

  eventDetails: any;
  // tslint:disable-next-line: no-input-rename
  @Input('track-pointer') data: any = {};

  constructor(private dataStorage: DataStorageService, private analyticsService: AnalyticsService) { }

  /**
   * Track Mouse Movement
   */
  trackMouseMoveEvent() {
    fromEvent(window, 'mousemove')
      .subscribe((e: MouseEvent) => {
        this.eventDetails = e;
        this.sendData();
      });
  }

  /**
   * Pushing Mouse Move details
   */
  public sendData(): void {
    const analyticsBean: AnalyticsBean =
      this.analyticsService.setAnalyticsData(this.data, this.eventDetails, 'Mouse Move', '');
    this.dataStorage.appendToAnalyticsArray(analyticsBean);
  }

}
