import { Directive, Input, HostListener } from '@angular/core';
import { DataStorageService } from '../../services/data-storage/data-storage.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';
import { AnalyticsService } from '../../services/analytics/analytics.service';

/**
 * Button Directive to track click event
 * Selector can be added to any HTML Element
 */
@Directive({
  // tslint:disable-next-line: directive-selector
  selector: '[track-btn]'
})
export class ButtonDirective {

  // Gets important data about the button explicitly from the application
  // tslint:disable-next-line: no-input-rename
  @Input('track-btn') data: any = {};

  eventDetails: any;

  /**
   * Button Tracking - Constructor
   * @param dataStorage - DataStorageService
   * @param analyticsService
   */
  constructor(private dataStorage: DataStorageService, private analyticsService: AnalyticsService) { }


  /**
   *  Listen to button click actions
   */
  @HostListener('click', ['$event']) onClick($event: any) {
    this.eventDetails = $event;
    setTimeout(() => {
      this.sendData();
    }, 10);
  }

  /** Sending data on button click */
  public sendData(): void {
    const analyticsBean: AnalyticsBean =
      this.analyticsService.setAnalyticsData(this.data, this.eventDetails, 'buttonClick', '');
    this.dataStorage.appendToAnalyticsArray(analyticsBean);
  }
}
