import { ErrorHandler, Injectable, Injector } from '@angular/core';
import { AnalyticsService } from '../analytics/analytics.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';

@Injectable()
export class GlobalErrorHandler implements ErrorHandler {
    constructor(private injector: Injector) {
        const analyticsService = this.injector.get(AnalyticsService);
        if (window.console && console.error) {
            const consoleErrorFnObject = console.error;
            console.error = function (...error: any[]) {
                const processedError = error.map(e => {
                    if (typeof (e) === 'object') {
                        return JSON.stringify(e);
                    } else {
                        return e;
                    }
                });
                const analyticsBean: AnalyticsBean = analyticsService.setAnalyticsData(processedError, {}, 'ConsoleError', '');
                analyticsService.publishConsoleErrors(analyticsBean);
                consoleErrorFnObject.call(console, error);
            };
        }
    }

    /** Implementing the method */
    handleError(error: any) { }

}
