import { Directive, OnChanges, HostListener, Input } from '@angular/core';
import { AnalyticsService } from '../../services/analytics/analytics.service';
import { DataStorageService } from '../../services/data-storage/data-storage.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';

@Directive({
    // tslint:disable-next-line: directive-selector
    selector: '[track-scroll]'
})
export class ScrollDirective implements OnChanges {

    // Gets important data about the component explicitly from the application
    // tslint:disable-next-line: no-input-rename
    @Input('track-scroll') data: any = {};

    constructor(
        private analyticsService: AnalyticsService,
        private dataStorage: DataStorageService
    ) { }

    // Capture the change in data
    ngOnChanges(changes: any) {
        this.data = changes.data.currentValue;
    }

    // Triggered when any scroll event occurs
    @HostListener('window:scroll', ['$event']) onScrollEvent($event: any) {
        setTimeout(() => {
            this.sendData($event);
        }, 100);
    }


    public sendData(event: any): void {
        const analyticsBean: AnalyticsBean =
            this.analyticsService.setAnalyticsData(this.data, event, 'scroll', '');
        this.dataStorage.appendToAnalyticsArray(analyticsBean);
    }

}
