import { Injectable } from '@angular/core';
import { AnalyticsService } from '../analytics/analytics.service';
import { HttpClient } from '@angular/common/http';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';

@Injectable({
  providedIn: 'root'
})
export class DataStorageService {

  allDataAnalyticsArray: Array<any> = [];
  allDataAnalytics: {
    pageUrl: string,
    eventValues: Array<any>
  };
  previousUrl: string;
  keys: Array<any> = [];
  eventCollector = new Map();
  constructor(private analyticalService: AnalyticsService, private http: HttpClient) { }
  private routeDetails: any = [];
  count = 0;
  setUrlKey(data: string) {
    let flag = 0;
    if (this.previousUrl === undefined) {
      this.eventCollector.set(data, []);
      this.previousUrl = data;
    } else if (!(data === this.previousUrl)) {
      for (const key of Array.from(this.eventCollector.keys())) {
        if (key === data) {
          flag = 1;
          break;
        }
      }
      if (flag === 0) {
        this.eventCollector.set(data, []);
      }
      this.previousUrl = data;
    }
  }
  appendToAnalyticsArray(data: AnalyticsBean) {
    this.eventCollector.get(this.previousUrl).push(data);
  }

  pushDataArrayToS3() {
    this.count++;
    // this.allDataAnalyticsMap = JSON.parse(JSON.stringify(Array.from(this.eventCollector.keys())));
    for (const key of Array.from(this.eventCollector.keys())) {
      this.allDataAnalytics = {
        pageUrl: key,
        eventValues: Array.from(this.eventCollector.get(key).values())
      };
      this.keys.push(key);
      if (this.allDataAnalytics.eventValues.length > 0) {
        this.analyticalService.pushData(this.allDataAnalytics);
      }
    }
    this.eventCollector.clear();
    for (const key of this.keys) {
      this.eventCollector.set(key, []);
    }
  }

  setRouteDetails(routeDetails: any) {
    this.routeDetails = routeDetails;
  }

  getRouteDetails() {
    return this.routeDetails;
  }

}
